
import { defineComponent } from "vue-demi";
import { mapGetters } from "vuex";
import { Filter } from '@element-plus/icons';
import { dynamicsObject } from "@/interfaces";

export default defineComponent({
  name: "TableFilters",
  emits: ['onFilters'],
  data() {
    return {
      showMenu: false,
      filters: [
        { id: 'civilServant', name: '', list: [
          { _id: 1, name: '', selected: false },
          { _id: 2, name: '', selected: false },
          { _id: 3, name: ' ', selected: false },
        ] },
        { id: 'risk', name: '-', list: [
          { _id: 'careful', name: '', selected: false },
          { _id: 'conservative', name: '', selected: false },
          { _id: 'moderate', name: '', selected: false },
          { _id: 'aggressive', name: '', selected: false },
          { _id: 'stocks', name: ' 100%', selected: false },
        ] },
        { id: 'term', name: ' ', list: [
          { _id: 1, name: '/', selected: false },
          { _id: -1, name: '/', selected: false },
        ] },
      ]
    }
  },
  methods: {
    selectFilter(filter: dynamicsObject, element: dynamicsObject) {
      for (const el of filter.list) {
        if (el._id !== element._id) el.selected = false;
      }
      element.selected = !element.selected;
    },
    onFilters() {
      const query: dynamicsObject = {civilServant: '', risk: '', term: ''};
      this.filters.forEach(filter => {
        filter.list.forEach((element: dynamicsObject) => {
          if (element.selected) query[filter.id] = element._id;
        })
      });
      this.$emit('onFilters', query);
    }
  },
  computed: mapGetters(["pendingRequest"]),
  components: {
    Filter
  }
});
